<template>
  <div class="box">
    <div class="box-logo">
      <img src="@/assets/login_logo_bg.png" class="login-img"/>
    </div>

    <div class="login-content">
      <input v-model="phone" bindblur="closeLocalAccount" bindinput="accountInput" bindfocus="showLocalAccount"
        type="text" class="input-print" maxlength="16" placeholder="请输入用户名/手机号"  />

      <input v-model="password" type="password" class="input-print" maxlength="16" placeholder="请输入登录密码"  />

      <div class="box-privacy">
        <div class="img-privacy-box">
          <img @click="bindPrivacy" class="img-privacy"
            :src=" privacy ? require('@/assets/check_box_checked_white_bg.png'): require('@/assets/check_box_uncheck_white_bg.png')" />
        </div>
        <div class="text-privacy">
          登录即代表同意<a :href="$store.state.baseUrl + '/static/notice/user_agreement.html'" target="_blank" class="user-privacy">《用户协议》</a> 和<a :href="$store.state.baseUrl + '/static/notice/supplier_privacy.html'" target="_blank" class="user-privacy">《隐私政策》</a>
        </div>
      </div>
      <button @click="clickLogin"  class="btn-login">登 录</button>
      <div>
        <div class="register" bindtap="bindRegister">注册</div>
        <div class="forget" bindtap="bindForget">忘记密码</div>
      </div>
    </div>
  </div>
</template>
<script>

import { login } from '@/http/api'

export default {
  name: 'Login',
  data () {
    return {
      pwd_visible: true,
      privacy: false, // 是否勾选登录协议
      phone: '',
      password: '',
      accountList: [], // 保存过的账号
      backupList: [],
      updatePwIndex: -1,
      popupShow: false,
      open_local_account: true

    }
  },
  methods: {
    bindPrivacy () {
      this.privacy = !this.privacy
    },
    clickLogin () {
      const username = this.phone
      const password = this.password
      if (!username) {
        this.$toast('请输入账号')
        return
      }
      if (!password) {
        this.$toast('请输入密码')
        return
      }
      if (!this.privacy) {
        this.$toast('请先同意登录协议')
      }
      const deviceId = 'vue_' + username + '_' + new Date().getTime()
      login({
        username: username,
        password: password,
        deviceId: deviceId
      }).then(res => {
        this.$store.commit('setDeviceId', deviceId)
        this.$router.replace('/index')
      }).catch(rej => {
        this.$toast(rej.msg)
      })
    }
  },
  mounted () {
  }
}
</script>
<style lang="scss" scoped>
.box{
    background-color: #42D18A;
    width: 100vw;
    height: 100vh;
}

.box-logo {
    width: 100%;
    height: 18.75rem;
}

.login-img {
    padding-top: 5rem;
    width: 50%;
    height: auto;
    margin: 0 auto;
    display: block;
}

.input-print {
    padding: .75rem;
    font-size: 1rem;
    color: #333333;
    border-radius: .25rem;
    background-color: #68D9A1;
    margin-bottom: .75rem;
    max-lines: 1;
    width: 90%;
    border: none;
}

input::placeholder {
    font-weight: 500;
    color: #51BE87;
    font-size: 1rem;
}

.login-content {
    padding-left:5%;
    padding-right: 5%;
}

.text-privacy {
    color: white;
    font-size: .875rem;
}

.btn-login {
    width: 100% !important;
    font-size: 1rem;
    padding: .6875rem;
    font-weight: 500;
    color: #42D18A;
    margin-top: 1.5rem;
    border-radius: .5rem;
    margin-bottom: 1.25rem;
    border: none;
    border-radius: .25rem;
}

.register {
    color: white;
    padding-top: .875rem;
    padding-bottom: .875rem;
    padding-right:.875rem;
    float: left;
}

.forget {
    color: white;
    padding-top: .875rem;
    padding-bottom:.875rem;
    padding-left: .875rem;
    float: right;
}

.box-privacy {
    margin-top: .9375rem;
    margin-bottom: .9375rem;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.img-privacy-box {
    padding: 0;
    margin-right: .5rem;
    font-size: 0;
}

.img-privacy {
    width: 1rem;
    height:1rem;
}

.user-privacy {
    text-decoration: underline;
    color: white;
}

.dialog-content {
    padding: 0 48rpx;
    min-height: 100rpx;
    font-size: 36rpx;
    line-height: 1.3;
    word-wrap: break-word;
    word-break: break-all;
    color: #666;
}

.dialog-tip {
    padding: 0 48rpx;
    font-size: 24rpx;
    text-align: left;
    margin-bottom: 26rpx;
    color: #808080;
}

.content-tip-critical {
    color: #42d18a;
}
</style>
